@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}boday{margin:0px;padding:0px;line-height:1px;color:black;font-family:'Roboto', sans-serif}.headerr{border-bottom:solid 1px #cc5017;background-color:#d7eff3;padding:0;margin-top:-20px;display:flex;justify-content:center;width:100vw;height:15vh}.headerr nav{width:50vw;height:15vh;background-color:pink;display:flex;justify-content:center}.headerr img{width:5vw;height:15vh;position:absolute;left:20.3%}

.task-bar{background-color:white;height:100vh;width:27vw;padding:74px 20px 0;position:relative}.task-bar .add-class-btn{cursor:pointer;position:relative;top:-35px}.task-bar .add-class-btn .plus{color:#dd4b39;font-size:18px}.task-bar .add-class-btn .add-text{color:#545454;font-size:14px;margin-left:15px}.task-bar .add-btn{background-color:#dd4c13;color:white;border:1px solid transparent;margin-right:5px;font-weight:bold;font-size:13px;padding:6px 12px 7px 12px;border-radius:3px !important;cursor:pointer}.task-bar .add-btn:disabled{background-color:gray}.task-bar .add-btn:active{transform:scale(0.9)}.task-bar .add-task-dailog{margin-top:10px}.task-bar .add-task-dailog input{width:100%;border:1px solid #ddd;background-color:white;border-radius:3px;height:35px;padding-left:10px;padding-right:10px;margin:3px;position:relative;top:-25px}.cancel-btn{all:unset;color:#555;cursor:pointer;font-size:14px;margin:0px 5px}h1{position:relative;top:-34px}.add-task-action-container{display:flex;justify-content:space-between}.icon-container{margin-top:20px}.task-item1{display:flex;justify-content:space-between}.task-item1:not(:last-child){border-bottom:1px solid red}

.side-bar{-webkit-user-select:none;-ms-user-select:none;user-select:none;width:16vw;height:100vh;background-color:#f1f2f3;position:absolute;top:12.9%;left:20.3%;padding-top:65px;border-right:1px solid #f4f5f6}.side-bar div{padding:15px;margin-bottom:10px;cursor:pointer}.side-bar div.active{background-color:white}.icon{margin-right:10px}

.content{display:flex;justify-content:center}

